import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { MdTitle } from 'react-icons/md';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { CopyIcon, TrashIcon } from '@mssgme/icons';
import { AclMenuPane } from '@mssgme/ui';

import { usePublish } from '../../../../hooks';
import SeoIcon from '../../../../icons/seoIcon.svg';

const menu = (t, page, website, canPublish) => {
    const isHomePage = website.homePageId === page._id;

    return [
        // {
        //     to: !website.enabled && !page.blocks.length ? 'no-blocks' : 'status',
        //     label: t('Page.View.Settings.items.status'),
        //     icon: FiToggleRight,
        //     preview: page.enabled ? t('ui:labels.yes') : t('ui:labels.no'),
        //     filter: (!website.scaffold && canPublish) || page.enabled,
        //     disabled: website.homePageId === page._id,
        //     'data-event': 'click-page-settings',
        //     'data-feature': 'status',
        // },
        {
            to: 'name',
            label: t('Page.View.Settings.items.name'),
            icon: MdTitle,
            preview: page.name,
            disabled: isHomePage,
            'data-event': 'click-page-settings',
            'data-feature': 'name',
        },
        {
            to: 'slug',
            label: t('Page.View.Settings.items.slug'),
            icon: MdTitle,
            preview: page.slug,
            filter: !isHomePage,
            'data-event': 'click-page-settings',
            'data-feature': 'slug',
        },
        {
            to: 'seo',
            label: t('Page.View.Settings.items.seo'),
            icon: SeoIcon,
            iconProps: { style: { display: 'block', width: 18, height: 18, color: '#333' } },
            restrictRule: 'page-seo:edit',
            restrictAllowClick: true,
            preview: page.seo.title || page.seo.description ? t('labels.changed') : t('labels.asDefault'),
            'data-event': 'click-page-settings',
            'data-feature': 'seo',
        },
        {
            to: 'clone',
            label: t('Page.View.Settings.items.clone'),
            icon: CopyIcon,
            restrictRule: 'page:create',
            restrictAllowClick: true,
            'data-event': 'click-page-settings',
            'data-feature': 'clone',
        },
        {
            to: 'delete',
            label: t('labels.page.delete'),
            labelColor: '#dc5960',
            icon: TrashIcon,
            iconColor: '#dc5960',
            disabled: isHomePage,
            'data-event': 'click-page-settings',
            'data-feature': 'delete',
        },
    ];
};

export function PageSettings() {
    const { t } = useTranslation('landings');
    const { website, page } = useWebsitePageBySlugs();
    const { canPublish } = usePublish(website, page);
    const items = useMemo(() => menu(t, page, website, canPublish), [t, website, page, canPublish]);

    return <AclMenuPane title={t('Page.View.Settings.heading')} items={items} />;
}
