import { useMemo } from 'react';

export const useWebsiteMenu = (website, menuItems = website.menu?.items) => useMemo(() => {
    const map = new Map(website.pages.map((page) => [page._id, page]));
    const present = [];
    const totalCurrent = menuItems?.length;
    let deleted = 0;
    let items = menuItems;

    if (totalCurrent) {
        for (const item of menuItems) {
            if (!map.has(item.page)) {
                deleted++;
                continue;
            }

            present.push(item);
            map.delete(item.page);
        }
    }

    const added = [...map.values()].map((page) => ({
        enabled: true,
        label: '',
        page: page._id,
    }));

    const totalPresent = present.length;
    const totalAdded = added.length;

    if ((totalPresent !== totalCurrent) || totalAdded) {
        if (totalAdded) {
            items = totalPresent ? [...present, ...added] : added;
        } else {
            items = present;
        }
    } else {
        items ||= present;
    }

    return {
        items,
        deleted,
        added: totalAdded,
    };
}, [menuItems, website.pages]);
